import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.tsx'
import './index.css'
import { debug, setupGlobalErrorHandlers, safeSync, safeAsync } from './lib/debug'
import { Buffer } from 'buffer'

// Make Buffer available globally
window.Buffer = Buffer

// Proper wallet extension conflict resolution
// Prevent multiple wallet extensions from conflicting
if (typeof window !== 'undefined') {
  // Wait for DOM to be ready
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', setupWalletConflictResolution)
  } else {
    setupWalletConflictResolution()
  }
}

function setupWalletConflictResolution() {
  try {
    // Check if ethereum already exists
    if (window.ethereum) {
      debug.info('✅ Ethereum provider already exists:', window.ethereum.constructor?.name || 'Unknown')
    } else {
      debug.info('🔧 No ethereum provider found yet, will wait for injection')
    }
    
    // Override console methods to filter out wallet extension noise
    const originalConsoleError = console.error
    const originalConsoleWarn = console.warn
    
    console.error = (...args) => {
      const message = args[0]?.toString() || ''
      // Filter out known wallet extension communication errors
      if (
        message.includes('StreamMiddleware') ||
        message.includes('solflare-detect-metamask') ||
        message.includes('Unknown response id') ||
        message.includes('ethereum') ||
        message.includes('MetaMask') ||
        message.includes('evmAsk') ||
        message.includes('inpage.js') ||
        message.includes('contentScript')
      ) {
        return // Don't log these specific wallet extension errors
      }
      originalConsoleError.apply(console, args)
    }
    
    console.warn = (...args) => {
      const message = args[0]?.toString() || ''
      // Filter out known wallet extension warnings
      if (
        message.includes('StreamMiddleware') ||
        message.includes('solflare-detect-metamask') ||
        message.includes('Unknown response id') ||
        message.includes('ethereum') ||
        message.includes('MetaMask') ||
        message.includes('evmAsk') ||
        message.includes('inpage.js') ||
        message.includes('contentScript')
      ) {
        return // Don't log these specific wallet extension warnings
      }
      originalConsoleWarn.apply(console, args)
    }
    
    
    debug.info('🔧 Wallet conflict resolution setup complete')
  } catch (error) {
    debug.error('❌ Failed to setup wallet conflict resolution:', error)
  }
}

// Setup global error handlers immediately
setupGlobalErrorHandlers()

debug.info('🚀 Starting React app initialization...')

// Check if DOM is ready
if (document.readyState === 'loading') {
  debug.info('📄 DOM still loading, waiting for DOMContentLoaded...')
  document.addEventListener('DOMContentLoaded', initializeApp)
} else {
  debug.info('📄 DOM already ready, initializing immediately...')
  initializeApp()
}

async function initializeApp() {
  debug.info('🔍 Looking for root element...')
  
  const rootElement = safeSync(() => document.getElementById('root'), 'getElementById')
  
  if (!rootElement) {
    debug.error('❌ Root element not found!', {
      bodyHTML: document.body.innerHTML,
      headHTML: document.head.innerHTML,
      readyState: document.readyState
    })
    
    document.body.innerHTML = `
      <div style="padding: 20px; color: red; background: white; font-family: monospace;">
        <h1>🚨 CRITICAL ERROR: Root element not found!</h1>
        <p>Expected: &lt;div id="root"&gt;&lt;/div&gt;</p>
        <p>Document ready state: ${document.readyState}</p>
        <pre>${document.documentElement.outerHTML}</pre>
      </div>
    `
    return
  }

  debug.info('✅ Root element found', { 
    tagName: rootElement.tagName, 
    id: rootElement.id,
    className: rootElement.className 
  })

  try {
    debug.info('🔧 Creating React root...')
    const root = safeSync(() => ReactDOM.createRoot(rootElement), 'createRoot')
    
    if (!root) {
      debug.error('❌ Failed to create React root')
      return
    }

    debug.info('🎨 Rendering React app...')
    
    // Wrap App in error boundary for extra safety
    const AppWithErrorBoundary = () => {
      try {
        return (
          <React.StrictMode>
            <App />
          </React.StrictMode>
        )
      } catch (error) {
        debug.error('❌ Error in App component render', error)
        return (
          <div style={{ padding: '20px', color: 'red', background: 'white' }}>
            <h1>App Render Error</h1>
            <pre>{String(error)}</pre>
          </div>
        )
      }
    }

    root.render(<AppWithErrorBoundary />)
    debug.info('✅ App rendered successfully!')
    
    // Verify the app actually rendered content
    setTimeout(() => {
      const rootContent = rootElement.innerHTML
      if (!rootContent || rootContent.trim() === '') {
        debug.error('❌ App rendered but root element is empty!', {
          rootContent,
          rootElement: rootElement.outerHTML
        })
      } else {
        debug.info('✅ App content verified', { 
          contentLength: rootContent.length,
          hasContent: rootContent.trim().length > 0
        })
      }
    }, 100)

  } catch (error) {
    debug.error('❌ Critical error during app initialization', error)
    
    rootElement.innerHTML = `
      <div style="padding: 20px; color: red; background: white; font-family: monospace;">
        <h1>🚨 REACT INITIALIZATION ERROR</h1>
        <pre>${String(error)}</pre>
        <p>Stack trace:</p>
        <pre>${error?.stack || 'No stack trace available'}</pre>
      </div>
    `
  }
}
